.Button {
  background-color: transparent;
  border: none;
  color: white;
  outline: none;
  cursor: pointer;
  font: inherit;
  padding: 10px;
  margin: 10px;
  font-weight: bold;
}

.Button:first-of-type {
  margin-left: 0;
  padding-left: 0;
}
.Button:disabled {
  color: #ccc;
  cursor: not-allowed;
}

.Success {
  color: #5c9210;
  box-shadow: 0 2px 3px #ccc;
  border: 1px solid #eee;
  border-radius: 5px;
}

.Danger {
  color: #944317;
  box-shadow: 0 2px 3px #ccc;
  border: 1px solid #eee;
  border-radius: 5px;
}
